import * as React from 'react';
import * as THREE from 'three';
import { useInstancedMesh, useModelGeometry } from '../../../core/hooks';
import { WallCellGeometryConfig } from '../../Configs/Cells/WallCellsConfig';
import MainMaterial from '../Materials/MainMaterial';

export type Props = {
  asset: string;
  wallCells: THREE.Object3D[];
};

function Wall(props: Props) {
  const { asset, wallCells } = props;
  const geometry = useModelGeometry(asset, WallCellGeometryConfig.rotation);
  const ref = useInstancedMesh(wallCells);
  return (
    <instancedMesh ref={ref} args={[geometry, undefined, wallCells.length]} receiveShadow castShadow>
      <MainMaterial />
    </instancedMesh>
  );
}

export default React.memo(Wall);
