import * as React from 'react';
import * as THREE from 'three';
import { useCubeMapContext } from '../CubeMapContext';
import CubeHelper from '../../Helpers/CubeHelper';
import { CellType } from '../../Enums/CellType';
import MainMaterial from '../Materials/MainMaterial';
import glbCoin from './coin.glb';
import { useInstancedMesh, useModelGeometry } from '../../../core/hooks';

export type Props = {};

function Coins(props: Props) {
  const {} = props;
  const { levelConfig } = useCubeMapContext();
  const { coinsObjects } = React.useMemo(() => {
    const itemPositions = CubeHelper.getItemPositions(levelConfig.map.sides, CellType.Coin);
    const coinsObjects = itemPositions.map(({ side, gridPosition: { x, y } }) => {
      const obj = new THREE.Object3D();
      obj.position.copy(CubeHelper.getPositionByGridAndSide(levelConfig.size, side, x, y));
      CubeHelper.setSideRotation(obj, side);
      return obj;
    });
    return { coinsObjects };
  }, []);
  const geometry = useModelGeometry(glbCoin);
  const ref = useInstancedMesh(coinsObjects);
  // console.log('Coins', { coinsObjects });
  return (
    <instancedMesh ref={ref} args={[geometry, undefined, coinsObjects.length]} receiveShadow castShadow>
      <MainMaterial />
    </instancedMesh>
  );
}

export default React.memo(Coins);
