import * as React from 'react';
import * as THREE from 'three';
import { ILevelConfig } from '../../Interfaces/ILevelConfig';
import GameplayConfig from '../../Configs/Main/GameplayConfig';

import glbEdge01 from './edge_01.glb';
import glbEdge02 from './edge_02.glb';
import glbEdge03 from './edge_03.glb';
import glbEdge04 from './edge_04.glb';
import Edge from './Edge';
import { EdgeDistanceConfig, EdgeModelsConfig, EdgeRotationConfig } from '../../Configs/Cells/EdgeCellsConfig';
import CubeHelper from '../../Helpers/CubeHelper';
import { CellType } from '../../Enums/CellType';
import ThreeJSHelper from '../../Helpers/ThreeJSHelper';

export type Props = {
  levelMap: ILevelConfig;
};

function Edges(props: Props) {
  const { levelMap } = props;

  const { edgeCellsByProbability } = React.useMemo(() => {
    const distance = new THREE.Vector3(
      (levelMap.size.x + 1) * 0.5 * GameplayConfig.grid.size,
      (levelMap.size.y + 1) * 0.5 * GameplayConfig.grid.size,
      (levelMap.size.z + 1) * 0.5 * GameplayConfig.grid.size
    );

    const edgeCells: THREE.Object3D[] = [];

    for (let i = 0; i < EdgeRotationConfig.length; i++) {
      const edgeConfig = EdgeRotationConfig[i];
      const edgeSize = levelMap.size[edgeConfig.axis];

      for (let j = 0; j < edgeSize; j++) {
        // console.log('Edges', { edgeConfig, j });
        const cellType = CubeHelper.getCellTypeBySymbol(levelMap.map.edges[edgeConfig.edge]![j]);

        if (cellType === CellType.Wall) {
          const edgeCell = new THREE.Object3D();

          edgeCell.position.x = EdgeDistanceConfig[i].x * distance.x;
          edgeCell.position.y = EdgeDistanceConfig[i].y * distance.y;
          edgeCell.position.z = EdgeDistanceConfig[i].z * distance.z;

          edgeCell.position[edgeConfig.axis] +=
            j * GameplayConfig.grid.size + GameplayConfig.grid.size * 0.5 - edgeSize * 0.5 * GameplayConfig.grid.size;

          edgeCell.rotation.set(edgeConfig.rotation.x, edgeConfig.rotation.y, edgeConfig.rotation.z);
          edgeCell.scale.set(GameplayConfig.grid.scale, GameplayConfig.grid.scale, GameplayConfig.grid.scale);

          edgeCells.push(edgeCell);
        }
      }
    }

    const edgeCellsByProbability = ThreeJSHelper.splitObjectsByProbability(edgeCells, EdgeModelsConfig.probabilities);

    return { edgeCellsByProbability };
  }, []);

  // console.log('Edges', { edgeCellsByProbability });
  return (
    <>
      <Edge asset={glbEdge01} edgeCells={edgeCellsByProbability[0]} />
      <Edge asset={glbEdge02} edgeCells={edgeCellsByProbability[1]} />
      <Edge asset={glbEdge03} edgeCells={edgeCellsByProbability[2]} />
      <Edge asset={glbEdge04} edgeCells={edgeCellsByProbability[3]} />
    </>
  );
}

export default React.memo(Edges);
