import { useLoader } from '@react-three/fiber';
import * as THREE from 'three';
import * as React from 'react';
import { GLTFLoader } from 'three/examples/jsm/Addons';
import ThreeJSHelper from '../scene/Helpers/ThreeJSHelper';

export function useModelGeometry(asset: string, rotation?: THREE.Euler) {
  const gltf = useLoader(GLTFLoader, asset);
  const geometry = React.useMemo(() => {
    const geometry = (gltf.scene.children[0] as THREE.Mesh).geometry.clone();
    if (rotation) {
      ThreeJSHelper.setGeometryRotation(geometry, rotation);
    }
    return geometry;
  }, [asset]);
  return geometry;
}

export function useInstancedMesh(objects: THREE.Object3D[]) {
  const ref = React.useRef<THREE.InstancedMesh>(null);
  React.useEffect(() => {
    const instancedMesh = ref.current;
    if (!instancedMesh) return;
    instancedMesh.count = objects.length;
    objects.forEach((object, index) => {
      object.updateMatrix();
      instancedMesh.setMatrixAt(index, object.matrix);
    });
    instancedMesh.instanceMatrix.needsUpdate = true;
  }, [objects]);
  return ref;
}
