import * as React from 'react';
import * as THREE from 'three';
import glbRoof01 from './roof_01.glb';
import glbRoof02 from './roof_02.glb';
import glbRoof03 from './roof_03.glb';
import glbRoof04 from './roof_04.glb';
import glbRoof05 from './roof_05.glb';
import glbRoof06 from './roof_06.glb';
import { CubeSide } from '../../Enums/CubeSide';
import { CubeSideAxisConfig } from '../../Configs/SideConfig';
import { ILevelConfig } from '../../Interfaces/ILevelConfig';
import CubeHelper from '../../Helpers/CubeHelper';
import { CellType } from '../../Enums/CellType';
import { setCellPosition } from '../CubeMapUtils';
import ArrayHelper from '../../Helpers/ArrayHelper';
import { Direction } from '../../Enums/Direction';
import GameplayConfig from '../../Configs/Main/GameplayConfig';
import ThreeJSHelper from '../../Helpers/ThreeJSHelper';
import { RoofModelsConfig } from '../../Configs/Cells/RoofCellsConfig';
import Roof from './Roof';

export type Props = {
  levelMap: ILevelConfig;
};

function Roofs(props: Props) {
  const { levelMap } = props;

  const { roofCellsByProbability } = React.useMemo(() => {
    const roofCells = [];

    for (const side in CubeSide) {
      const cubeSide = CubeSide[side as keyof typeof CubeSide];
      const cubeSideAxisConfig = CubeSideAxisConfig[cubeSide];
      const sizeX: number = levelMap.size[cubeSideAxisConfig.xAxis];
      const sizeY: number = levelMap.size[cubeSideAxisConfig.yAxis];

      for (let row = 0; row < sizeY; row++) {
        for (let column = 0; column < sizeX; column++) {
          const cellType = CubeHelper.getCellTypeBySymbol(levelMap.map.sides[cubeSide]![row][column]);

          if (cellType === CellType.Wall || cellType === CellType.WallSpike) {
            const roof = new THREE.Object3D();

            setCellPosition(levelMap, roof, cubeSide, row, column);
            CubeHelper.setSideRotation(roof, cubeSide);
            const randomDirection: Direction = ArrayHelper.getRandomArrayElement([
              Direction.Up,
              Direction.Down,
              Direction.Left,
              Direction.Right,
            ]);
            CubeHelper.setRotationByDirection(roof, cubeSide, randomDirection);
            roof.scale.set(GameplayConfig.grid.scale, GameplayConfig.grid.scale, GameplayConfig.grid.scale);

            roofCells.push(roof);
          }
        }
      }
    }

    const roofCellsByProbability: THREE.Object3D[][] = ThreeJSHelper.splitObjectsByProbability(
      roofCells,
      RoofModelsConfig.probabilities
    );
    return { roofCellsByProbability };
  }, []);

  return (
    <>
      <Roof asset={glbRoof01} roofCells={roofCellsByProbability[0]} />
      <Roof asset={glbRoof02} roofCells={roofCellsByProbability[1]} />
      <Roof asset={glbRoof03} roofCells={roofCellsByProbability[2]} />
      <Roof asset={glbRoof04} roofCells={roofCellsByProbability[3]} />
      <Roof asset={glbRoof05} roofCells={roofCellsByProbability[4]} />
      <Roof asset={glbRoof06} roofCells={roofCellsByProbability[5]} />
    </>
  );
}

export default React.memo(Roofs);
