import * as React from 'react';
import * as THREE from 'three';
import { IWallSpikeConfig } from '../IEnemyConfig';
import { useCubeMapContext } from '../../CubeMapContext';
import MapHelper from '../../../Helpers/MapHelper';
import CubeHelper from '../../../Helpers/CubeHelper';
import { Direction } from '../../../Enums/Direction';
import { CellType } from '../../../Enums/CellType';
import { CubeSide } from '../../../Enums/CubeSide';
import GameplayConfig from '../../../Configs/Main/GameplayConfig';
import { CubeSideAxisConfig, SideVectorConfig } from '../../../Configs/SideConfig';
import { ILevelConfig } from '../../../Interfaces/ILevelConfig';
import { useInstancedMesh, useModelGeometry } from '../../../../core/hooks';
import glbWall01 from '../../Walls/wall_01.glb';
import { WallCellGeometryConfig } from '../../../Configs/Cells/WallCellsConfig';
import MainMaterial from '../../Materials/MainMaterial';
import gblWallSpikes from './wall_spikes.glb';
import { WallSpikesGeneralConfig } from '../WallSpikesConfig';

export type Props = {
  configs: IWallSpikeConfig[];
};

function useLevelMap(levelConfig: ILevelConfig) {
  return React.useMemo(() => {
    const { sides, edges } = MapHelper.copyMapSidesAndEdges(levelConfig);
    const levelMap = {
      size: new THREE.Vector3(levelConfig.size.x, levelConfig.size.y, levelConfig.size.z),
      map: { sides, edges },
    };
    MapHelper.replaceExtraSymbolsInMap(levelMap);
    return levelMap;
  }, [levelConfig]);
}

function checkToCreateWall(
  levelMap: ReturnType<typeof useLevelMap>,
  cellType: CellType,
  cubeSide: CubeSide,
  x: number,
  y: number,
  direction: Direction
): THREE.Object3D | null {
  if (cellType === CellType.Empty) {
    const wall = new THREE.Object3D();

    setCellPosition(levelMap, wall, cubeSide, y, x);
    CubeHelper.setSideRotation(wall, cubeSide);
    CubeHelper.setRotationByDirection(wall, cubeSide, direction);
    wall.scale.set(GameplayConfig.grid.scale, GameplayConfig.grid.scale, GameplayConfig.grid.scale);

    return wall;
  }

  return null;
}

function setCellPosition(
  levelMap: ReturnType<typeof useLevelMap>,
  cell: THREE.Object3D,
  cubeSide: CubeSide,
  x: number,
  y: number
): void {
  const cubeSideAxisConfig = CubeSideAxisConfig[cubeSide];

  const distance: number = (levelMap.size[cubeSideAxisConfig.zAxis] + 1) * 0.5 * GameplayConfig.grid.size;
  const offsetX: number = (levelMap.size[cubeSideAxisConfig.xAxis] - 1) * 0.5 * GameplayConfig.grid.size;
  const offsetY: number = (levelMap.size[cubeSideAxisConfig.yAxis] - 1) * 0.5 * GameplayConfig.grid.size;

  cell.position.x = SideVectorConfig[cubeSide].x * distance;
  cell.position.y = SideVectorConfig[cubeSide].y * distance;
  cell.position.z = SideVectorConfig[cubeSide].z * distance;

  cell.position[cubeSideAxisConfig.xAxis] +=
    y * GameplayConfig.grid.size * cubeSideAxisConfig.xFactor - offsetX * cubeSideAxisConfig.xFactor;
  cell.position[cubeSideAxisConfig.yAxis] +=
    x * GameplayConfig.grid.size * cubeSideAxisConfig.yFactor - offsetY * cubeSideAxisConfig.yFactor;
}

function WallSpikes(props: Props) {
  const { configs } = props;
  const { levelConfig } = useCubeMapContext();

  const levelMap = useLevelMap(levelConfig);

  const { wallCells } = React.useMemo(() => {
    const wallCells = [];

    for (let i = 0; i < configs.length; i++) {
      const config = configs[i];
      const { x, y } = config.position!;
      const cubeSide = config.side!;
      const sideMap: string[][] = levelMap.map.sides[cubeSide]!;

      if (y > 0 && sideMap[y - 1][x] && !config.directions.includes(Direction.Up)) {
        const cellType = CubeHelper.getCellTypeBySymbol(sideMap[y - 1][x]);
        const wall = checkToCreateWall(levelMap, cellType, cubeSide, x, y, Direction.Up);

        if (wall) {
          wallCells.push(wall);
        }
      }

      if (y < levelMap.size.y - 1 && sideMap[y + 1][x] && !config.directions.includes(Direction.Down)) {
        const cellType: CellType = CubeHelper.getCellTypeBySymbol(sideMap[y + 1][x]);
        const wall = checkToCreateWall(levelMap, cellType, cubeSide, x, y, Direction.Down);

        if (wall) {
          wallCells.push(wall);
        }
      }

      if (x > 0 && sideMap[y][x - 1] && !config.directions.includes(Direction.Left)) {
        const cellType: CellType = CubeHelper.getCellTypeBySymbol(sideMap[y][x - 1]);
        const wall = checkToCreateWall(levelMap, cellType, cubeSide, x, y, Direction.Left);

        if (wall) {
          wallCells.push(wall);
        }
      }

      if (x < levelMap.size.x - 1 && sideMap[y][x + 1] && !config.directions.includes(Direction.Right)) {
        const cellType: CellType = CubeHelper.getCellTypeBySymbol(sideMap[y][x + 1]);
        const wall = checkToCreateWall(levelMap, cellType, cubeSide, x, y, Direction.Right);

        if (wall) {
          wallCells.push(wall);
        }
      }
    }
    return { wallCells };
  }, [levelMap]);

  const { spikeObjects } = React.useMemo(() => {
    const spikeObjects = [];
    for (let i = 0; i < configs.length; i++) {
      const config: IWallSpikeConfig = configs[i];
      const { x, y } = config.position!;
      const cubeSide = config.side!;

      for (let i = 0; i < config.directions.length; i++) {
        const direction = config.directions[i];

        const spike = new THREE.Object3D();

        setCellPosition(levelMap, spike, cubeSide, y, x);
        CubeHelper.setSideRotation(spike, cubeSide);
        CubeHelper.setRotationByDirection(spike, cubeSide, direction);
        spike.scale.set(GameplayConfig.grid.scale, GameplayConfig.grid.scale, GameplayConfig.grid.scale);
        spikeObjects.push(spike);
      }
    }
    return { spikeObjects };
  }, [configs, levelMap]);

  const geometryWall = useModelGeometry(glbWall01, WallCellGeometryConfig.rotation);
  const refWalls = useInstancedMesh(wallCells);

  const geometrySpike = useModelGeometry(gblWallSpikes, WallSpikesGeneralConfig.modelStartRotation);
  const refSpikes = useInstancedMesh(spikeObjects);
  // const geometry = useModelGeometry(glbWall01, WallCellGeometryConfig.rotation);
  console.log('WallSpikes', { configs });
  return (
    <group>
      <instancedMesh ref={refWalls} args={[geometryWall, undefined, wallCells.length]} receiveShadow castShadow>
        <MainMaterial />
      </instancedMesh>
      <instancedMesh ref={refSpikes} args={[geometrySpike, undefined, spikeObjects.length]} receiveShadow castShadow>
        <MainMaterial />
      </instancedMesh>
    </group>
  );
}

export default React.memo(WallSpikes);
