import * as React from 'react';
import * as THREE from 'three';
import { ILevelConfig } from '../../Interfaces/ILevelConfig';
import GameplayConfig from '../../Configs/Main/GameplayConfig';
import {
  CubeSideByEdge,
  EdgeDirectionForwardConfig,
  EdgeDistanceConfig,
  EdgeRotationConfig,
  EdgeWallModelsConfig,
  EdgeWallsRotation,
} from '../../Configs/Cells/EdgeCellsConfig';
import CubeHelper from '../../Helpers/CubeHelper';
import { CellType } from '../../Enums/CellType';
import { Direction } from '../../Enums/Direction';
import EdgeWall from './EdgesWall';
import glbEdgeWall01 from './edge_wall_01.glb';
import ThreeJSHelper from '../../Helpers/ThreeJSHelper';

export type Props = {
  levelMap: ILevelConfig;
};

function EdgesWalls(props: Props) {
  const { levelMap } = props;

  const { edgeWallCellsByProbability } = React.useMemo(() => {
    const distance = new THREE.Vector3(
      (levelMap.size.x + 1) * 0.5 * GameplayConfig.grid.size,
      (levelMap.size.y + 1) * 0.5 * GameplayConfig.grid.size,
      (levelMap.size.z + 1) * 0.5 * GameplayConfig.grid.size
    );

    const edgeWallCells: THREE.Object3D[] = [];

    for (let i = 0; i < EdgeRotationConfig.length; i++) {
      const edgeConfig = EdgeRotationConfig[i];
      const cubeSide = CubeSideByEdge[edgeConfig.edge];
      const edgeWallRotation = EdgeWallsRotation[edgeConfig.edge];
      const wallSymbol = CubeHelper.getCellSymbolByType(CellType.Wall);
      const fullEdgeMap = [...levelMap.map.edges[edgeConfig.edge]!];
      fullEdgeMap.unshift(wallSymbol);
      fullEdgeMap.push(wallSymbol);

      const edgeSize: number = levelMap.size[edgeConfig.axis] + 2;

      for (let column = 0; column < edgeSize; column++) {
        const cellType: CellType = CubeHelper.getCellTypeBySymbol(fullEdgeMap[column]);

        if (column < fullEdgeMap.length - 1 && cellType === CellType.Wall) {
          const rightCellType: CellType = CubeHelper.getCellTypeBySymbol(fullEdgeMap[column + 1]);

          if (rightCellType === CellType.Empty) {
            let emptyCellsCount = 0;
            for (let i = column + 1; i < edgeSize; i++) {
              const cellType: CellType = CubeHelper.getCellTypeBySymbol(fullEdgeMap[i]);
              if (cellType === CellType.Empty) {
                emptyCellsCount++;
              } else {
                break;
              }
            }

            const edgeCell = new THREE.Object3D();

            edgeCell.position.x = EdgeDistanceConfig[i].x * distance.x;
            edgeCell.position.y = EdgeDistanceConfig[i].y * distance.y;
            edgeCell.position.z = EdgeDistanceConfig[i].z * distance.z;

            const cellOffset: number = EdgeDirectionForwardConfig[edgeConfig.edge] === false ? emptyCellsCount + 1 : 0;
            edgeCell.position[edgeConfig.axis] =
              (column + cellOffset) * GameplayConfig.grid.size +
              GameplayConfig.grid.size * 0.5 -
              edgeSize * 0.5 * GameplayConfig.grid.size;

            edgeCell.rotation.set(edgeConfig.rotation.x, edgeConfig.rotation.y, edgeConfig.rotation.z);
            CubeHelper.setRotationByDirection(edgeCell, cubeSide, Direction.Up);
            edgeCell.rotateOnWorldAxis(edgeWallRotation.axis, edgeWallRotation.rightRotation);

            edgeCell.scale.set(GameplayConfig.grid.scale, GameplayConfig.grid.scale, GameplayConfig.grid.scale);

            edgeWallCells.push(edgeCell);
          }
        }

        if (column > 0 && cellType === CellType.Wall) {
          const leftCellType: CellType = CubeHelper.getCellTypeBySymbol(fullEdgeMap[column - 1]);

          if (leftCellType === CellType.Empty) {
            let emptyCellsCount = 0;
            for (let i = column - 1; i >= 0; i--) {
              const cellType: CellType = CubeHelper.getCellTypeBySymbol(fullEdgeMap[i]);
              if (cellType === CellType.Empty) {
                emptyCellsCount++;
              } else {
                break;
              }
            }

            const edgeCell = new THREE.Object3D();

            edgeCell.position.x = EdgeDistanceConfig[i].x * distance.x;
            edgeCell.position.y = EdgeDistanceConfig[i].y * distance.y;
            edgeCell.position.z = EdgeDistanceConfig[i].z * distance.z;

            const cellOffset: number = EdgeDirectionForwardConfig[edgeConfig.edge] === false ? emptyCellsCount + 1 : 0;
            edgeCell.position[edgeConfig.axis] +=
              (column - cellOffset) * GameplayConfig.grid.size +
              GameplayConfig.grid.size * 0.5 -
              edgeSize * 0.5 * GameplayConfig.grid.size;

            edgeCell.rotation.set(edgeConfig.rotation.x, edgeConfig.rotation.y, edgeConfig.rotation.z);
            CubeHelper.setRotationByDirection(edgeCell, cubeSide, Direction.Down);
            edgeCell.rotateOnWorldAxis(edgeWallRotation.axis, edgeWallRotation.leftRotation);

            edgeCell.scale.set(GameplayConfig.grid.scale, GameplayConfig.grid.scale, GameplayConfig.grid.scale);

            edgeWallCells.push(edgeCell);
          }
        }
      }
    }

    const edgeWallCellsByProbability: THREE.Object3D[][] = ThreeJSHelper.splitObjectsByProbability(
      edgeWallCells,
      EdgeWallModelsConfig.probabilities
    );

    return { edgeWallCellsByProbability };
  }, [levelMap]);

  // console.log('EdgesWalls', { edgeWallCellsByProbability });
  return (
    <>
      <EdgeWall asset={glbEdgeWall01} edgeWallCells={edgeWallCellsByProbability[0]} />
    </>
  );
}

export default React.memo(EdgesWalls);
