import * as React from 'react';
import { useCubeMapContext } from '../CubeMapContext';
import { EnemyType } from '../../Enums/EnemyType';
import { EnemyCellType } from './EnemiesGeneralConfig';
import CubeHelper from '../../Helpers/CubeHelper';
import { EnemyConfigMap, IFloorSpikeConfig, IWallSpikeConfig } from './IEnemyConfig';
import WallSpikes from './EnemyByType/WallSpikes';
import FloorSpikes from './EnemyByType/FloorSpikes';

export type Props = {};

function Enemies(props: Props) {
  const {} = props;

  const { levelConfig } = useCubeMapContext();

  const elements = React.useMemo(() => {
    const enemiesTypes = Object.keys(levelConfig.enemies!);
    const elements: React.ReactNode[] = [];
    for (const enemyType of enemiesTypes) {
      const enemies = levelConfig.enemies![enemyType as EnemyType]!;
      const cellType = EnemyCellType[enemyType as EnemyType];
      const enemyPositions = CubeHelper.getItemWithIDPositions(levelConfig.map.sides, cellType);
      const enemyConfigs: EnemyConfigMap[EnemyType][] = [];
      for (const enemy of enemies) {
        // console.log('enemy', enemyType, enemy);
        const enemyPosition = enemyPositions.find(({ id }) => id === enemy.id);
        if (enemyPosition) {
          enemy.side = enemyPosition.side;
          enemy.position = enemyPosition.gridPosition;
          enemyConfigs.push(enemy);
        } else {
          console.error(`Enemy ${enemyType} with id ${enemy.id} not found`);
        }
      }

      if (enemyType === EnemyType.WallSpike) {
        elements.push(<WallSpikes key={enemyType} configs={enemies as IWallSpikeConfig[]} />);
      } else if (enemyType === EnemyType.FloorSpike) {
        elements.push(<FloorSpikes key={enemyType} configs={enemies as IFloorSpikeConfig[]} />);
      }
    }
    return elements;
  }, [levelConfig]);

  // console.log('Enemies', elements.length);
  return <>{elements}</>;
}

export default React.memo(Enemies);
