import { MeshProps, useLoader } from '@react-three/fiber';
import * as THREE from 'three';
import * as React from 'react';
import { GLTFLoader } from 'three/examples/jsm/Addons';
import MainMaterial from '../Materials/MainMaterial';

export type Props = MeshProps & {
  asset: string;
};

function Corner(props: Props) {
  const { asset, ...meshProps } = props;
  const gltf = useLoader(GLTFLoader, asset);
  const geometry = React.useMemo(() => (gltf.scene.children[0] as THREE.Mesh).geometry, [gltf]);
  // console.log('Corner', { gltf });
  return (
    <>
      {/* <primitive object={gltf.scene} /> */}
      <mesh geometry={geometry} {...meshProps}>
        <MainMaterial />
      </mesh>
    </>
  );
}

export default React.memo(Corner);
