import * as React from 'react';
import * as THREE from 'three';
import { useInstancedMesh, useModelGeometry } from '../../../core/hooks';
import MainMaterial from '../Materials/MainMaterial';
import { RoofCellsGeometryConfig } from '../../Configs/Cells/RoofCellsConfig';

export type Props = {
  asset: string;
  roofCells: THREE.Object3D[];
};

function Roof(props: Props) {
  const { asset, roofCells } = props;
  const geometry = useModelGeometry(asset, RoofCellsGeometryConfig.rotation);
  const ref = useInstancedMesh(roofCells);
  return (
    <instancedMesh ref={ref} args={[geometry, undefined, roofCells.length]} receiveShadow castShadow>
      <MainMaterial />
    </instancedMesh>
  );
}

export default React.memo(Roof);
