import * as THREE from 'three';
import * as React from 'react';
import asset from './dungeon_texture.png';
import { useLoader } from '@react-three/fiber';

function MainMaterial() {
  const texture = useLoader(THREE.TextureLoader, asset.src);
  // console.log('MainMaterial', { texture });
  return <meshPhongMaterial map={texture} />;
}

export default React.memo(MainMaterial);
