import * as React from 'react';
import * as THREE from 'three';
import { ILevelConfig, IMapConfig } from '../../Interfaces/ILevelConfig';
import { CubeSide } from '../../Enums/CubeSide';
import { CubeSideAxisConfig } from '../../Configs/SideConfig';
import CubeHelper from '../../Helpers/CubeHelper';
import { CellType } from '../../Enums/CellType';
import { checkCellForWallInFullMap, getWallDirections, setCellPosition } from '../CubeMapUtils';
import GameplayConfig from '../../Configs/Main/GameplayConfig';
import { Direction } from '../../Enums/Direction';
import ThreeJSHelper from '../../Helpers/ThreeJSHelper';
import { WallModelsConfig } from '../../Configs/Cells/WallCellsConfig';
import Wall from './Wall';

import glbWall01 from './wall_01.glb';
import glbWall02 from './wall_02.glb';
import glbWall03 from './wall_03.glb';
import glbWall04 from './wall_04.glb';
import glbWall05 from './wall_05.glb';

export type Props = {
  levelMap: ILevelConfig;
  fullMap: IMapConfig;
};

function Walls(props: Props) {
  const { levelMap, fullMap: fullMapAll } = props;

  const { wallCellsByProbability } = React.useMemo(() => {
    const wallCells = [];

    for (const side in CubeSide) {
      const cubeSide: CubeSide = CubeSide[side as keyof typeof CubeSide];
      const cubeSideAxisConfig = CubeSideAxisConfig[cubeSide];
      const sizeX = levelMap.size[cubeSideAxisConfig.xAxis];
      const sizeY = levelMap.size[cubeSideAxisConfig.yAxis];

      for (let row = 0; row < sizeY; row++) {
        for (let column = 0; column < sizeX; column++) {
          const cellType = CubeHelper.getCellTypeBySymbol(levelMap.map.sides[cubeSide]![row][column]);

          if (cellType === CellType.Wall) {
            const wallDirections = getWallDirections(levelMap.map.sides[cubeSide]!, row, column);

            for (let i = 0; i < wallDirections.length; i++) {
              const wallDirection = wallDirections[i];

              const wall = new THREE.Object3D();

              setCellPosition(levelMap, wall, cubeSide, row, column);
              CubeHelper.setSideRotation(wall, cubeSide);
              CubeHelper.setRotationByDirection(wall, cubeSide, wallDirection);
              wall.scale.set(GameplayConfig.grid.scale, GameplayConfig.grid.scale, GameplayConfig.grid.scale);

              wallCells.push(wall);
            }
          }
        }
      }

      const fullMap: string[][] = fullMapAll[cubeSide]!;

      for (let row = 0; row < fullMap.length; row++) {
        for (let column = 0; column < fullMap[row].length; column++) {
          const cellType: CellType = CubeHelper.getCellTypeBySymbol(fullMap[row][column]);
          let wall: THREE.Object3D | null = null;

          if (row === 0 && column !== 0 && column !== fullMap[row].length - 1 && cellType === CellType.Wall) {
            const bottomCellType: CellType = CubeHelper.getCellTypeBySymbol(fullMap[row + 1][column]);
            wall = checkCellForWallInFullMap(levelMap, bottomCellType, cubeSide, row, column, Direction.Down);
          }

          if (
            row === fullMap.length - 1 &&
            column !== 0 &&
            column !== fullMap[row].length - 1 &&
            cellType === CellType.Wall
          ) {
            const topCellType: CellType = CubeHelper.getCellTypeBySymbol(fullMap[row - 1][column]);
            wall = checkCellForWallInFullMap(levelMap, topCellType, cubeSide, row, column, Direction.Up);
          }

          if (column === 0 && row !== 0 && row !== fullMap.length - 1 && cellType === CellType.Wall) {
            const rightCellType: CellType = CubeHelper.getCellTypeBySymbol(fullMap[row][column + 1]);
            wall = checkCellForWallInFullMap(levelMap, rightCellType, cubeSide, row, column, Direction.Right);
          }

          if (
            column === fullMap[row].length - 1 &&
            row !== 0 &&
            row !== fullMap.length - 1 &&
            cellType === CellType.Wall
          ) {
            const leftCellType: CellType = CubeHelper.getCellTypeBySymbol(fullMap[row][column - 1]);
            wall = checkCellForWallInFullMap(levelMap, leftCellType, cubeSide, row, column, Direction.Left);
          }

          if (wall) {
            wallCells.push(wall);
          }
        }
      }
    }

    const wallCellsByProbability: THREE.Object3D[][] = ThreeJSHelper.splitObjectsByProbability(
      wallCells,
      WallModelsConfig.probabilities
    );

    return { wallCellsByProbability };
  }, []);

  return (
    <>
      <Wall asset={glbWall01} wallCells={wallCellsByProbability[0]} />
      <Wall asset={glbWall02} wallCells={wallCellsByProbability[1]} />
      <Wall asset={glbWall03} wallCells={wallCellsByProbability[2]} />
      <Wall asset={glbWall04} wallCells={wallCellsByProbability[3]} />
      <Wall asset={glbWall05} wallCells={wallCellsByProbability[4]} />
    </>
  );
}

export default React.memo(Walls);
