import * as React from 'react';
import { ILevelConfig } from '../Interfaces/ILevelConfig';
import CubeHelper from '../Helpers/CubeHelper';
import { CellType } from '../Enums/CellType';
import { ICubePosition } from '../Interfaces/ICubeConfig';
import { Object3DProps } from '@react-three/fiber';
import { ObjectsRotationBySideConfig } from '../Configs/SideConfig';

declare namespace useCubeMapContextValue {
  export type Params = {
    levelConfig: ILevelConfig;
  };
}

function useCubeMapContextValue(params: useCubeMapContextValue.Params) {
  const { levelConfig } = params;

  const getObjectTransform = React.useCallback(
    (cubePosition: ICubePosition): Pick<Object3DProps, 'position' | 'rotation'> => {
      const {
        side,
        gridPosition: { x, y },
      } = cubePosition;
      return {
        position: CubeHelper.getPositionByGridAndSide(levelConfig.size, side, x, y),
        rotation: ObjectsRotationBySideConfig[side],
      };
    },
    [levelConfig]
  );

  const getCubePosition = React.useCallback(
    (cellType: CellType) => {
      const itemPositions = CubeHelper.getItemPositions(levelConfig.map.sides, cellType);
      return itemPositions[0];
    },
    [levelConfig]
  );

  const posPlayerInitial = React.useMemo(() => getCubePosition(CellType.Start), [getCubePosition]);

  const [posPlayer, setPosPlayer] = React.useState(posPlayerInitial);

  return {
    levelConfig,
    posPlayer,
    setPosPlayer,
    getCubePosition,
    getObjectTransform,
  };
}

export type CubeMapContext = ReturnType<typeof useCubeMapContextValue>;

const Context = React.createContext<CubeMapContext>({} as any);

export declare namespace CubeMapContextProvider {
  export type Params = {
    levelConfig: ILevelConfig;
    children?: React.ReactNode;
  };
}

export function CubeMapContextProvider(params: CubeMapContextProvider.Params) {
  const { levelConfig, children } = params;
  const value = useCubeMapContextValue({ levelConfig });
  return <Context.Provider value={value}>{children}</Context.Provider>;
}

export function useCubeMapContext() {
  return React.useContext(Context);
}
