import * as React from 'react';
import * as THREE from 'three';
import { useInstancedMesh, useModelGeometry } from '../../../core/hooks';
import MainMaterial from '../Materials/MainMaterial';
import { FloorCellsGeometryConfig } from '../../Configs/Cells/FloorCellsConfig';

export type Props = {
  asset: string;
  floorCells: THREE.Object3D[];
};

function Floor(props: Props) {
  const { asset, floorCells } = props;
  const geometry = useModelGeometry(asset, FloorCellsGeometryConfig.rotation);
  const ref = useInstancedMesh(floorCells);
  // console.log('Floor', { floorCells, geometry });
  return (
    <instancedMesh ref={ref} args={[geometry, undefined, floorCells.length]} receiveShadow>
      <MainMaterial />
    </instancedMesh>
  );
}

export default React.memo(Floor);
