import * as React from 'react';
import * as THREE from 'three';
import glbCorner01 from './corner_01.glb';
import glbCorner02 from './corner_02.glb';
import glbCorner03 from './corner_03.glb';
import glbCorner04 from './corner_04.glb';
import glbCorner05 from './corner_05.glb';
import Corner from './Corner';
import { MeshProps } from '@react-three/fiber';
import GameplayConfig from '../../Configs/Main/GameplayConfig';
import { ILevelConfig } from '../../Interfaces/ILevelConfig';

export type Props = {
  levelMap: ILevelConfig;
};

type Vector3Array = [x: number, y: number, z: number];

function Corners(props: Props) {
  const { levelMap } = props;

  const distance = new THREE.Vector3(
    (levelMap.size.x + 1) * 0.5 * GameplayConfig.grid.size,
    (levelMap.size.y + 1) * 0.5 * GameplayConfig.grid.size,
    (levelMap.size.z + 1) * 0.5 * GameplayConfig.grid.size
  );

  const commonProps: MeshProps = {
    receiveShadow: true,
    castShadow: true,
    scale: [GameplayConfig.grid.scale, GameplayConfig.grid.scale, GameplayConfig.grid.scale],
  };
  const applyDistance = React.useCallback(
    ([x, y, z]: Vector3Array) => [x * distance.x, y * distance.y, z * distance.z] as Vector3Array,
    [distance]
  );
  return (
    <>
      {/* FrontTopLeft */}
      <Corner
        {...commonProps}
        asset={glbCorner01}
        position={applyDistance([-1, 1, 1])}
        rotation={[0, -Math.PI * 0.5, 0]}
      />
      {/* FrontTopRight */}
      <Corner {...commonProps} asset={glbCorner02} position={applyDistance([1, 1, 1])} rotation={[0, 0, 0]} />
      {/* FrontDownLeft */}
      <Corner
        {...commonProps}
        asset={glbCorner03}
        position={applyDistance([-1, -1, 1])}
        rotation={[0, -Math.PI * 0.5, -Math.PI * 0.5]}
      />
      {/* FrontDownRight */}
      <Corner
        {...commonProps}
        asset={glbCorner01}
        position={applyDistance([1, -1, 1])}
        rotation={[0, 0, -Math.PI * 0.5]}
      />
      {/* BackTopLeft */}
      <Corner {...commonProps} asset={glbCorner04} position={applyDistance([-1, 1, -1])} rotation={[0, Math.PI, 0]} />
      {/* BackTopRight */}
      <Corner
        {...commonProps}
        asset={glbCorner01}
        position={applyDistance([1, 1, -1])}
        rotation={[0, Math.PI * 0.5, 0]}
      />
      {/* BackDownLeft */}
      <Corner
        {...commonProps}
        asset={glbCorner01}
        position={applyDistance([-1, -1, -1])}
        rotation={[0, Math.PI, -Math.PI * 0.5]}
      />
      {/* BackDownRight */}
      <Corner
        {...commonProps}
        asset={glbCorner05}
        position={applyDistance([1, -1, -1])}
        rotation={[0, Math.PI * 0.5, -Math.PI * 0.5]}
      />
    </>
  );
}

export default React.memo(Corners);
