import mitt from 'mitt';
import * as React from 'react';
import * as THREE from 'three';
import { ButtonConfig } from './scene/Configs/ButtonsConfig';
import { ButtonType } from './scene/Enums/ButtonType';
import { GameState } from './scene/Enums/GameState';
import { CubeState } from './scene/Enums/CubeState';
import { PlayerCharacterState } from './scene/Enums/PlayerCharacterState';
import { MovementDirectionByButtonConfig } from './scene/Configs/PlayerCharacterConfig';
import { MoveDirection } from './scene/Enums/MoveDirection';

declare namespace useSceneContextValue {
  export type Params = {
    camera: THREE.PerspectiveCamera;
  };
}

type Event = {
  onButtonPressed: ButtonType;
};

function useSceneContextValue(params: useSceneContextValue.Params) {
  const { camera } = params;

  const [gameState, setGameState] = React.useState(GameState.Active);
  const [playeActivaited, setPlayerActivated] = React.useState(false);
  const [cubeState, setCubeState] = React.useState(CubeState.Idle);
  const [playerCharacterState, setPlayerCharacterState] = React.useState(PlayerCharacterState.Idle);

  const emitter = React.useMemo(() => mitt<Event>(), []);

  const ref = React.useRef<HTMLDivElement>(null);
  const refNextMoveDirection = React.useRef<MoveDirection>();
  React.useEffect(() => {
    const el = ref.current;
    if (!el) return;
    // keydown event listener from el
    const handler = (event: KeyboardEvent) => {
      for (const value in ButtonType) {
        const buttonType = ButtonType[value as keyof typeof ButtonType];
        const config = ButtonConfig[buttonType];
        if (config.keyCode && config.keyCode.includes(event.code)) {
          console.log('onButtonPressed', buttonType);
          emitter.emit('onButtonPressed', buttonType);

          const moveDirection = MovementDirectionByButtonConfig[buttonType] || undefined;
          if (moveDirection) {
            if (
              cubeState === CubeState.Rotating ||
              (playerCharacterState === PlayerCharacterState.Moving && refNextMoveDirection.current === null)
            ) {
              refNextMoveDirection.current = moveDirection;
              return;
            }

            if (cubeState === CubeState.Idle && playerCharacterState === PlayerCharacterState.Idle) {
              //
            }
          }
        }
      }
    };
    el.addEventListener('keydown', handler);
    return () => {
      el.removeEventListener('keydown', handler);
    };
  }, [emitter]);
  return {
    ref,
    emitter,
    camera,
  };
}

export type SceneContext = Omit<ReturnType<typeof useSceneContextValue>, 'ref'>;

const Context = React.createContext<SceneContext>({} as SceneContext);

export declare namespace SceneContextProvider {
  export type Props = useSceneContextValue.Params & {
    children?: React.ReactNode;
  };
}

export function SceneContextProvider(props: SceneContextProvider.Props) {
  const { children, ...params } = props;
  const { ref, ...value } = useSceneContextValue(params);
  return (
    <div ref={ref}>
      <Context.Provider value={value}>{children}</Context.Provider>
    </div>
  );
}

export function useSceneContext() {
  return React.useContext(Context);
}
