import * as THREE from 'three';
import * as React from 'react';
import { EdgeWallCellGeometryConfig } from '../../Configs/Cells/EdgeCellsConfig';
import MainMaterial from '../Materials/MainMaterial';
import { useInstancedMesh, useModelGeometry } from '../../../core/hooks';

export type Props = {
  asset: string;
  edgeWallCells: THREE.Object3D<THREE.Object3DEventMap>[];
};

function EdgeWall(props: Props) {
  const { asset, edgeWallCells } = props;
  const geometry = useModelGeometry(asset, EdgeWallCellGeometryConfig.rotation);
  const ref = useInstancedMesh(edgeWallCells);
  return (
    <instancedMesh ref={ref} args={[geometry, undefined, edgeWallCells.length]} receiveShadow castShadow>
      <MainMaterial />
    </instancedMesh>
  );
}

export default React.memo(EdgeWall);
