'use client';

import { OrbitControls } from '@react-three/drei';
import { Canvas } from '@react-three/fiber';
import * as React from 'react';
import * as THREE from 'three';
import CubeMap from './scene/Cube/CubeMap';
import { LevelsConfig } from './scene/Configs/LevelsConfig';
import { LevelType } from './scene/Enums/LevelType';
import { SceneContextProvider } from './SceneContext';
import * as PIXI from 'pixi.js';
import { DeviceState } from './scene/Enums/DeviceState';
import CameraConfig from './scene/Configs/Main/CameraConfig';

export type Props = {
  width: number;
  height: number;
};

function Scene(props: Props) {
  const { width, height } = props;
  const levelConfig = LevelsConfig[LevelType.Level01];
  const { camera } = React.useMemo(() => {
    // const camera = new THREE.PerspectiveCamera(50, 1, 1, 70);
    const camera = new THREE.PerspectiveCamera(50, width / height, 1, 70);
    const isMobile = Boolean(PIXI.isMobile);
    const deviceState: DeviceState = isMobile ? DeviceState.Mobile : DeviceState.Desktop;
    const distanceConfig = CameraConfig.distanceFromCube.dynamic[deviceState];

    // levelConfig
    // this.currentMaxSideSize = Math.max(map.length, map[0].length);

    camera.position.z = distanceConfig.start + 2 * distanceConfig.sideCoefficient;

    return { camera };
  }, []);

  // console.log('Scene', { width, height, levelConfig, camera });
  return (
    <SceneContextProvider camera={camera}>
      <Canvas
        style={{
          border: '1px solid black',
          width,
          height,
        }}
        // camera={{
        //   position: [0, 2, 5],
        //   fov: 75,
        //   near: 0.1,
        //   far: 1000,
        // }}
        camera={camera}
      >
        <React.Suspense>
          {levelConfig && <CubeMap levelConfig={levelConfig} />}
          <OrbitControls enableDamping />
          <ambientLight intensity={1} color={0xffffff} />
          <directionalLight intensity={1.3} color={0xffffff} position={[5, 5, 5]} />
          <fog args={[0x666666, 18, 24]} />
        </React.Suspense>
      </Canvas>
    </SceneContextProvider>
  );
}

export default React.memo(Scene);
