import * as React from 'react';
import * as THREE from 'three';
import { IFloorSpikeConfig } from '../IEnemyConfig';
import CubeHelper from '../../../Helpers/CubeHelper';
import { useCubeMapContext } from '../../CubeMapContext';
import { useInstancedMesh, useModelGeometry } from '../../../../core/hooks';
import glbFloorSpikesBase from './floor_spikes_base.glb';
import glbFloorSpikes from './floor_spikes.glb';
import MainMaterial from '../../Materials/MainMaterial';
import { FloorSpikesGeneralConfig } from '../FloorSpikesConfig';
import { CubeSideAxisConfig } from '../../../Configs/SideConfig';

export type Props = {
  configs: IFloorSpikeConfig[];
};

function FloorSpikes(props: Props) {
  const { configs } = props;

  const { levelConfig } = useCubeMapContext();
  const { floorSpikeBaseObjects } = React.useMemo(() => {
    const floorSpikeBaseObjects: THREE.Object3D[] = [];

    for (let i = 0; i < configs.length; i++) {
      const config = configs[i];
      const { x, y } = config.position!;
      const side = config.side!;

      const floorSpike: THREE.Object3D = new THREE.Object3D();

      const position: THREE.Vector3 = CubeHelper.getPositionByGridAndSide(levelConfig.size, side, x, y);

      floorSpike.position.copy(position);
      CubeHelper.setSideRotation(floorSpike, side);

      floorSpikeBaseObjects.push(floorSpike);
    }
    return { floorSpikeBaseObjects };
  }, [configs]);

  const { floorSpikeObjects } = React.useMemo(() => {
    const floorSpikeObjects: THREE.Object3D[] = [];

    for (let i = 0; i < configs.length; i++) {
      const config = configs[i];
      const { x, y } = config.position!;
      const side = config.side!;

      const floorSpike: THREE.Object3D = new THREE.Object3D();

      const position: THREE.Vector3 = CubeHelper.getPositionByGridAndSide(levelConfig.size, side, x, y);

      const cubeSideAxisConfig = CubeSideAxisConfig[side];

      position[cubeSideAxisConfig.xAxis] += FloorSpikesGeneralConfig.spikesStartPosition.x * cubeSideAxisConfig.xFactor;
      position[cubeSideAxisConfig.yAxis] += FloorSpikesGeneralConfig.spikesStartPosition.y * cubeSideAxisConfig.yFactor;
      position[cubeSideAxisConfig.zAxis] += FloorSpikesGeneralConfig.spikesStartPosition.z * cubeSideAxisConfig.zFactor;

      floorSpike.position.copy(position);
      CubeHelper.setSideRotation(floorSpike, side);

      floorSpikeObjects.push(floorSpike);
      // this.spikeInstanceConfig.push({ mapConfig: config, instanceId: i, isActive: false, startPosition: position, time: 0 });
    }

    return { floorSpikeObjects };
  }, [configs]);

  const geometryBase = useModelGeometry(glbFloorSpikesBase, FloorSpikesGeneralConfig.geometryBaseRotation);
  const refBases = useInstancedMesh(floorSpikeBaseObjects);
  const geometrySpike = useModelGeometry(glbFloorSpikes, FloorSpikesGeneralConfig.geometrySpikeRotation);
  const refSpikes = useInstancedMesh(floorSpikeObjects);
  console.log('FloorSpikes', { configs });
  return (
    <group>
      <instancedMesh
        ref={refBases}
        args={[geometryBase, undefined, floorSpikeBaseObjects.length]}
        receiveShadow
        castShadow
      >
        <MainMaterial />
      </instancedMesh>
      <instancedMesh
        ref={refSpikes}
        args={[geometrySpike, undefined, floorSpikeObjects.length]}
        receiveShadow
        castShadow
      >
        <MainMaterial />
      </instancedMesh>
    </group>
  );
}

export default React.memo(FloorSpikes);
