import * as React from 'react';
import * as THREE from 'three';
import { CubeSide } from '../../Enums/CubeSide';
import { CubeSideAxisConfig } from '../../Configs/SideConfig';
import { ILevelConfig } from '../../Interfaces/ILevelConfig';
import CubeHelper from '../../Helpers/CubeHelper';
import { CellType } from '../../Enums/CellType';
import GameplayConfig from '../../Configs/Main/GameplayConfig';
import ArrayHelper from '../../Helpers/ArrayHelper';
import { Direction } from '../../Enums/Direction';
import ThreeJSHelper from '../../Helpers/ThreeJSHelper';
import { FloorModelsConfig } from '../../Configs/Cells/FloorCellsConfig';

import glbFloor01 from './floor_01.glb';
import glbFloor02 from './floor_02.glb';
import glbFloor03 from './floor_03.glb';
import glbFloor04 from './floor_04.glb';
import glbFloor05 from './floor_05.glb';
import glbFloor06 from './floor_06.glb';
import Floor from './Floor';
import { setCellPosition } from '../CubeMapUtils';

export type Props = {
  levelMap: ILevelConfig;
};

function Floors(props: Props) {
  const { levelMap } = props;

  const { floorCellsByProbability } = React.useMemo(() => {
    const floorCells: THREE.Object3D[] = [];

    for (const side in CubeSide) {
      const cubeSide = CubeSide[side as keyof typeof CubeSide];
      const cubeSideAxisConfig = CubeSideAxisConfig[cubeSide];
      const sizeX = levelMap.size[cubeSideAxisConfig.xAxis];
      const sizeY = levelMap.size[cubeSideAxisConfig.yAxis];

      for (let row = 0; row < sizeY; row++) {
        for (let column = 0; column < sizeX; column++) {
          const cellType = CubeHelper.getCellTypeBySymbol(levelMap.map.sides[cubeSide]![row][column]);

          if (cellType === CellType.Empty) {
            const floor = new THREE.Object3D();

            setCellPosition(levelMap, floor, cubeSide, row, column);
            CubeHelper.setSideRotation(floor, cubeSide);
            const randomDirection: Direction = ArrayHelper.getRandomArrayElement([
              Direction.Up,
              Direction.Down,
              Direction.Left,
              Direction.Right,
            ]);
            CubeHelper.setRotationByDirection(floor, cubeSide, randomDirection);
            floor.scale.setScalar(GameplayConfig.grid.scale);

            floorCells.push(floor);
          }
        }
      }
    }

    const floorCellsByProbability: THREE.Object3D[][] = ThreeJSHelper.splitObjectsByProbability(
      floorCells,
      FloorModelsConfig.probabilities
    );

    // console.log('Floors.useMemo', { floorCellsByProbability });
    return { floorCellsByProbability };
  }, [levelMap]);

  // console.log('Floors', { floorCellsByProbability });
  return (
    <>
      <Floor asset={glbFloor01} floorCells={floorCellsByProbability[0]} />
      <Floor asset={glbFloor02} floorCells={floorCellsByProbability[1]} />
      <Floor asset={glbFloor03} floorCells={floorCellsByProbability[2]} />
      <Floor asset={glbFloor04} floorCells={floorCellsByProbability[3]} />
      <Floor asset={glbFloor05} floorCells={floorCellsByProbability[4]} />
      <Floor asset={glbFloor06} floorCells={floorCellsByProbability[5]} />
    </>
  );
}

export default React.memo(Floors);
