import * as React from 'react';
import { useCubeMapContext } from '../CubeMapContext';
import { CellType } from '../../Enums/CellType';
import MainMaterial from '../Materials/MainMaterial';
import glbFinishObject from './finish-object.glb';
import { useModelGeometry } from '../../../core/hooks';

export type Props = {};

function FinishLevelObject(props: Props) {
  const {} = props;
  const { getCubePosition, getObjectTransform } = useCubeMapContext();

  const posFinish = getCubePosition(CellType.Finish);

  const geometry = useModelGeometry(glbFinishObject);
  return (
    <group {...getObjectTransform(posFinish)}>
      <mesh geometry={geometry} castShadow>
        <MainMaterial />
      </mesh>
      <pointLight
        args={[0xff0000, 2, 100]}
        castShadow
        shadow-mapSize={[128, 128]}
        shadow-camera-near={0.1}
        shadow-camera-far={3}
        shadow-bias={-0.0001}
      />
    </group>
  );
}

export default React.memo(FinishLevelObject);
