import * as React from 'react';
import * as THREE from 'three';
import { useModelGeometry } from '../../../core/hooks';
import { useLoader } from '@react-three/fiber';

import glbGhost from './ghost.glb';
import pngGhost from './Ghost_BaseColor.png';
import pngNormal from './Ghost_Normal.png';
import GameplayConfig from '../../Configs/Main/GameplayConfig';
import { PlayerCharacterGeneralConfig } from '../../Configs/PlayerCharacterGeneralConfig';
import DebugBodyPlayerMaterial from '../Materials/DebugBodyPlayerMaterial';
import { OBB } from 'three/examples/jsm/Addons';
import DebugConfig from '../../Configs/Main/DebugConfig';
import { useCubeMapContext } from '../CubeMapContext';
import gsap from 'gsap';
import { Back } from 'gsap';

export type Props = {};

function PlayerCharacter(props: Props) {
  const {} = props;
  const geometry = useModelGeometry(glbGhost, new THREE.Euler(Math.PI * 0.5, Math.PI, 0));
  const texture = useLoader(THREE.TextureLoader, pngGhost.src);
  texture.flipY = false;
  texture.colorSpace = THREE.SRGBColorSpace;
  const normal = useLoader(THREE.TextureLoader, pngNormal.src);
  normal.flipY = false;

  const viewScale: number = PlayerCharacterGeneralConfig.scale;

  const refLight = React.useRef<THREE.PointLight>(null);
  React.useLayoutEffect(() => {
    const pointLight = refLight.current!;
    if (!pointLight) return;
    pointLight.shadow.mapSize.width = 128;
    pointLight.shadow.mapSize.height = 128;
    pointLight.shadow.camera.near = 0.1;
    pointLight.shadow.camera.far = 5;
    pointLight.shadow.bias = -0.001;
  }, []);

  const refView = React.useRef<THREE.Mesh>(null);
  const [size, setSize] = React.useState<THREE.Vector3 | null>(null);
  React.useLayoutEffect(() => {
    const view = refView.current!;
    if (!view) return;
    const size = new THREE.Box3().setFromObject(view).getSize(new THREE.Vector3());
    // console.log('PlayerCharacter', { size });
    setSize(size);
  }, []);

  const refBody = React.useRef<THREE.Mesh>(null);
  React.useLayoutEffect(() => {
    const body = refBody.current!;
    if (!body) return;
    // console.log('PlayerCharacter', { body });
    body.geometry.computeBoundingBox();
    body.geometry.userData.obb = new OBB().fromBox3(body.geometry.boundingBox!);
    body.userData.obb = new OBB();
    body.visible = true;
    if (DebugConfig.gameplay.physicalBody) {
      body.visible = true;
    }
  }, []);

  const { posPlayer, getObjectTransform } = useCubeMapContext();

  const refRoot = React.useRef<THREE.Group>(null);
  React.useEffect(() => {
    const root = refRoot.current!;
    if (!root) return;
    // root.visible = false;
    root.scale.set(0, 0, 0);

    gsap.to(root.scale, {
      x: 1,
      y: 1,
      z: 1,
      duration: 0.3,
      ease: Back.easeOut,
      onComplete: () => {
        console.log('PlayerCharacter', 'gsap.to', 'onComplete');
        // return setIsActive(true);
      },
    });
  }, []);

  // console.log('PlayerCharacter', { geometry, texture });
  return (
    <group ref={refRoot} {...getObjectTransform(posPlayer)}>
      {/* viewGroup */}
      <group>
        {/* view */}
        <mesh
          ref={refView}
          geometry={geometry}
          castShadow
          receiveShadow
          scale={[
            GameplayConfig.grid.scale * viewScale,
            GameplayConfig.grid.scale * viewScale,
            GameplayConfig.grid.scale * viewScale,
          ]}
        >
          <meshStandardMaterial map={texture} normalMap={normal} emissiveMap={texture} transparent />
        </mesh>
        <pointLight args={[0xffffff, 5, 10, 2]} position={[0, 0, 0.3]} castShadow />
        {size && (
          <>
            {/* body */}
            <mesh ref={refBody}>
              <boxGeometry args={[size.x, size.y, size.z]} />
              <DebugBodyPlayerMaterial />
            </mesh>
          </>
        )}
      </group>
    </group>
  );
}

export default React.memo(PlayerCharacter);
